let sortOrder = 'asc'; // Estado de orden, 'asc' o 'desc'

function sortTable(column) {
    const table = document.querySelector('.table tbody');
    const rows = Array.from(table.rows);
    const isAsc = sortOrder === 'asc';

    // Ordenar las filas
    rows.sort((a, b) => {
        const aText = a.querySelector(`.column-${column}`).textContent.trim();
        const bText = b.querySelector(`.column-${column}`).textContent.trim();

        return isAsc ? aText.localeCompare(bText) : bText.localeCompare(aText);
    });

    // Alternar el orden para la próxima vez
    sortOrder = isAsc ? 'desc' : 'asc';

    // Vaciar la tabla y agregar las filas ordenadas
    table.innerHTML = '';
    rows.forEach(row => table.appendChild(row));
}



