// Menu Plegable
document.addEventListener('DOMContentLoaded', function () {
    const toggleMenuButton = document.getElementById('toggle-menu');
    const headerWrap = document.querySelector('#header');
    const wrapper = document.getElementById('wrapper');
    const titles = document.querySelectorAll('.menu-text');
    let isCollapsed = false;
    let initialMarginLeft = 0;

    // Imprime el ancho actual de la ventana
    function logScreenSize() {
    }

    // Inicializa el margen del wrapper basado en el ancho del header
    function initializeWrapperMargin() {
        initialMarginLeft = headerWrap.offsetWidth;
        updateWrapperMargin();
    }

    // Actualiza el margen y la visibilidad de los títulos según el estado colapsado o expandido
    function updateWrapperMargin() {
        if (isCollapsed) {
            wrapper.style.marginLeft = '85px';
        } else {
            wrapper.style.marginLeft = `${initialMarginLeft}px`;
        }

        titles.forEach(title => {
            title.style.display = isCollapsed ? 'none' : 'block';
        });

    }

    // Ajusta la lógica del menú solo para pantallas mayores a 1200px
    function applyLogicIfLargeScreen() {
        logScreenSize(); // Logea el tamaño de pantalla

        if (window.innerWidth >= 1200) {
            if (toggleMenuButton && headerWrap && wrapper) {
                initializeWrapperMargin(); // Establece el margen inicial

                // Alterna el estado del menú con cada clic
                toggleMenuButton.addEventListener('click', function () {
                    isCollapsed = !isCollapsed;
                    headerWrap.classList.toggle('collapsed-menu', isCollapsed);

                    setTimeout(() => {
                        updateWrapperMargin();
                    }, 0);
                });

            } else {
                console.error('El botón, el header-wrap o el wrapper no se encontró.');
            }
        } else {
            wrapper.style.marginLeft = '0'; // Resetea el margen para pantallas pequeñas
        }
    }

    applyLogicIfLargeScreen();
    window.addEventListener('resize', applyLogicIfLargeScreen);
});
